async function executeFilter(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const range = params.range ? worksheet.getRange(params.range) : worksheet.getUsedRange();
  
  // Apply autofilter
  range.worksheet.autoFilter.apply(range);
  
  await context.sync();
  
  return {
    success: true,
    message: '필터가 적용되었습니다.'
  };
}

async function executeInsert(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const type = params.type || 'row';
  const position = params.position || 1;
  const count = params.count || 1;
  
  if (type === 'row') {
    const range = worksheet.getRangeByIndexes(position - 1, 0, count, 1);
    range.insert(Excel.InsertShiftDirection.down);
  } else {
    const range = worksheet.getRangeByIndexes(0, position - 1, 1, count);
    range.insert(Excel.InsertShiftDirection.right);
  }
  
  await context.sync();
  
  return {
    success: true,
    message: `${count}개의 ${type === 'row' ? '행' : '열'}이 삽입되었습니다.`
  };
}

async function executeDelete(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const type = params.type || 'row';
  const position = params.position || 1;
  const count = params.count || 1;
  
  if (type === 'row') {
    const range = worksheet.getRangeByIndexes(position - 1, 0, count, 1);
    range.delete(Excel.DeleteShiftDirection.up);
  } else {
    const range = worksheet.getRangeByIndexes(0, position - 1, 1, count);
    range.delete(Excel.DeleteShiftDirection.left);
  }
  
  await context.sync();
  
  return {
    success: true,
    message: `${count}개의 ${type === 'row' ? '행' : '열'}이 삭제되었습니다.`
  };
}

async function executeFormula(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const range = params.range ? worksheet.getRange(params.range) : context.workbook.getSelectedRange();
  
  if (!params.formula) {
    return { success: false, error: '수식을 지정해주세요.' };
  }
  
  range.formulas = [[params.formula]];
  await context.sync();
  
  return {
    success: true,
    message: '수식이 적용되었습니다.'
  };
}

